// wagmi 配置文件
import { http, createConfig } from 'wagmi'
import { mainnet, sepolia, goerli } from 'wagmi/chains'

// 创建 wagmi 配置
export const config = createConfig({
  // 设置支持的链
  chains: [mainnet, sepolia, goerli],
  // 设置传输层
  transports: {
    [mainnet.id]: http(),
    [sepolia.id]: http(),
    [goerli.id]: http(),
  },
}) 